import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Home from '../pages/Home'; // Default import
import Profile from '../pages/Profile'; // Default import
import NotFound from '../pages/NotFound'; // Default import
import LoginPage from '../pages/LoginPage'; // Default import
import Navbar from './Navbar';  // Default import

function App() {
  return (
    <Router>
      <Navbar />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/login" component={LoginPage} />
        <Route path="/profile" component={Profile} />
        <Route component={NotFound} />
      </Switch>
    </Router>
  );
}

export default App;
